#--Account-mainDiv {
  width: calc(100% - 20px);
  height: calc(100% - 20px);
  background-color: transparent;
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: flex-start;
  gap: 10px;
  position: relative
}

._container-account {
  width: unset;
  height: unset;
  background-color: rgb(33, 40, 50, 0.1);
  border-radius: 20px;
  backdrop-filter: blur(50px);
  position: absolute;
}

#extender{
  width: 100%;
  height: 0.01px;
  background-color: transparent;
}

#mainDiv-linkedApps {
  width: 100%;
  border-radius: 20px;
  height: 170px;
  position: relative;
}

#mainDiv-modify {
  width: 100%;
  border-radius: 20px;
  height: 170px;
  position: relative;
}
#modify-mark{
  width: 100px;
  height: 40px;
  position: absolute;
  border-radius: 100px;
  background-color: rgb(49, 71, 94, 0.5);
  bottom: 7px;
  right: 7px;
  border: none;
  transition: 0.6s;
  cursor: pointer;
  color: rgb(245, 245, 245, 0.5);
  font-family: 'GSansMedium';
  text-align: left;
  padding-left: 15px;
}
#modify-mark img {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

#mainDiv-autoLogin {
  width: 100%;
  aspect-ratio: 1/0.5;
  border-radius: 20px;
  right: 0px;
  position: relative;
  min-width: 160px
}

#mainDiv-logout {
  width: 100%;
  aspect-ratio: 1/0.5;
  border-radius: 20px;
  right: 0px;
  position: relative;
  min-width: 160px
}
#logout-mark{
  width: 40px;
  height: 40px;
  position: absolute;
  border-radius: 100px;
  background-color: #FF393D;
  bottom: 7px;
  right: 7px;
  border: none;
  transition: 0.6s;
  cursor: pointer;
}
#logout-mark img {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.--prop-title {
  display: block;
  font-size: 20px;
  color: #F5F5F5;
  font-family: 'GSansBlack';
  top: 5px;
  left: 10px;
  position: absolute
}

.--prop-content {
  display: block;
  font-size: 14px;
  color: rgb(245, 245, 245);
  font-family: 'GSansMedium';
  position: relative;
  left: 10px;
  top: 35px;
  width: calc(100% - 40px)
}
#autoLogin-mark{
  width: 40px;
  height: 40px;
  position: absolute;
  border-radius: 100px;
  background-color: #007AFF;
  bottom: 7px;
  right: 7px;
  border: none;
  transition: 0.6s;
  cursor: pointer;
}
#autoLogin-mark img {
  width: 25px;
  height: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 1170px) {
  #--Account-mainDiv {
    width: calc(100% - 70px);
    height: calc(100% - 115px);
    background-color: transparent;
    margin-top: 80px;
    margin-left: 35px;
    border-radius: 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    gap: 20px;
    position: relative
  }
  #mainDiv-linkedApps {
    width: calc(70% - 10px);
    border-radius: 30px;
  }

  #mainDiv-modify {
    width: calc(70% - 10px);
    border-radius: 30px;
  }
  
  #mainDiv-autoLogin {
    width: calc(30% - 10px);
    aspect-ratio: 1/1;
    border-radius: 30px;
  }
  #autoLogin-mark{
    width: 50px;
    height: 50px;
    position: absolute;
    border-radius: 100px;
    background-color: #007AFF;
    bottom: 10px;
    right: 10px;
    border: none;
    transition: 0.6s;
    cursor: pointer;
  }
  #autoLogin-mark img {
    width: 27px;
    height: 27px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }


  #mainDiv-logout {
    width: calc(30% - 10px);
    aspect-ratio: 1/1;
    border-radius: 30px;
  }
  #logout-mark{
    width: 50px;
    height: 50px;
    position: absolute;
    border-radius: 100px;
    background-color: #FF393D;
    bottom: 10px;
    right: 10px;
    border: none;
    transition: 0.6s;
    cursor: pointer;
  }
  #logout-mark img {
    width: 27px;
    height: 27px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  #modify-mark{
    width: 120px;
    height: 50px;
    position: absolute;
    border-radius: 100px;
    background-color: rgb(49, 71, 94, 0.5);
    bottom: 10px;
    right: 10px;
    border: none;
    transition: 0.6s;
    cursor: pointer;
    z-index: 100;
    color: rgb(245, 245, 245, 0.5);
    font-family: 'GSansMedium';
    font-size: 17px;
    text-align: left;
    padding-left: 15px;
  }
  #modify-mark img {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }

  .--prop-title {
    display: block;
    font-size: 30px;
    color: #F5F5F5;
    font-family: 'GSansBlack';
    top: 10px;
    left: 20px;
    position: absolute
  }
  .--prop-content {
    display: block;
    font-size: 18px;
    color: rgb(245, 245, 245);
    font-family: 'GSansMedium';
    position: relative;
    left: 20px;
    top: 55px;
    width: calc(100% - 40px)
  }

}